import { Col, Divider, Row } from "antd";
import Loading from "./Loading";

export function MyBox(props: any) {
    return (
        <div className="my-box">
            <Row gutter={12}>
                <Col span={12}>
                    <Divider orientation="left" style={{borderTopColor: "#000"}}>
                        <h2 style={{ fontWeight: "bold" }}>{props.title}</h2>
                    </Divider>
                </Col>
                <Col span={12} style={{ textAlign: "right", paddingTop: "15px" }}>
                    {props.buttonList !== undefined ? props.buttonList() : ""}
                </Col>
            </Row>
            <Loading isLoading={props.isLoading} />
            <div className="my-box-children" style={{ opacity: props.isLoading === true ? 0.2 : 1 }}>
                {props.children}
            </div>
        </div>
    );
}

export function Box(props: any) {
    return (
        <div className={`${props.showBox !== false ? "box" : "box-no-border"} ${props.className}`} style={props.style}>
            <Loading isLoading={props.isLoading} />
            <div style={{ display: props.isLoading === true ? "none" : "" }}>
                {props.children}
            </div>
        </div>
    );
}

export function BoxNoTitle(props: any) { // child Title
    return (
        <div className={`box ${props.className}`}>
            <div>
                {props.children}
            </div>
        </div>
    );
}